import React, { useEffect, useMemo, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import css from "../css/wiki.module.css"
import axios from "axios"
import cookies from "cookie-universal"

const Wiki = () => {
    const [ mobile, setMobile ] = useState(false)
    const [ prefix, setPrefix ] = useState("")
    const [ topic, setTopic ] = useState([])
    const [ search, setSearch ] = useState("")
    const [ loading, setLoading ] = useState(true)
    const Cookies = useMemo(() => new cookies(), [])
    const params = useParams()

    useState(() => {
        document.title = "MCWIKI | Wiki"
    }, [])

    useEffect(() => {
        setMobile(Cookies.get("viewType") === "ng" ? true : false)
    }, [Cookies])

    useEffect(() => {
        setSearch("")
        axios.get(`https://raw.githubusercontent.com/MineHighVN/wikisource/main/${params.topic ? `${params.topic}/index.json` : `wiki.json`}`)
        .then(res => {
            if (!params.topic) {
                setPrefix(res.data.prefix)
                setTopic(res.data.topic)
            } else {
                setPrefix(`/wiki/${params.topic}`)
                setTopic(res.data)
            }
            setLoading(false)
        })
        .catch(err => {
            console.log(err)
            setLoading(false)
        })
    }, [params])

    return (
        <div className='container'>
            <input onChange={e => setSearch(e.target.value)} value={search} className={css.input} placeholder="Nhp ni dung bn mun tm kim vo y"/>
            {loading && <center><p>ANG TI...</p></center>}
            <div className={mobile ? css.mobileWiki : css.wiki}>
                {topic && topic.map((elm,index) => {
                    if (!(elm.title.toLowerCase().includes(search.toLowerCase()) ||
                    elm.description.toLowerCase().includes(search.toLowerCase()))) return ""
                    return (
                        <Link to={`${prefix}${elm.path}`} key={index}>
                            <p>{elm.title}</p>
                            <div><p>{elm.description}</p></div>
                        </Link>
                    )
                })}
            </div>
        </div>
    )
}

export default Wiki