import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import "quill/dist/quill.snow.css"
import 'highlight.js/styles/github-dark.css'

const WikiViewer = () => {
    const params = useParams()
    const [ content, setContent ] = useState("")
    const [ loading, setLoading ] = useState(true)

    useState(() => {
        document.title = `MCWIKI | ${params.id}`
    }, [])

    useEffect(() => {
        axios.get(`https://raw.githubusercontent.com/MineHighVN/wikisource/main/${`${params.topic}/${params.id}`}`)
        .then(res => {
            setContent(res.data)
            setLoading(false)
        })
        .catch(err => {
            console.log(err)
            setLoading(false)
        })
    }, [params])

    return (
        <div className='ql-snow'>
            {loading && <center><p>ANG TI...</p></center>}
            <div className='container ql-editor' dangerouslySetInnerHTML={{__html: content}}></div>
        </div>
    )
}

export default WikiViewer