import { HashRouter as Router, Routes, Route } from "react-router-dom"
import Main from "./views/Main";
import cookies from "cookie-universal"
import getDeviceType from "./tools/getDeviceType";
import { useEffect, useMemo, useState } from "react";

function App() {
    const Cookies = useMemo(() => new cookies(), [])
    const [ MB, ] = useState(/tablet|mobile/i.test(getDeviceType()))

    useEffect(() => {
        if (!Cookies.get("viewType"))
            if (MB) {
                Cookies.set("viewType","ng", { maxAge: Math.floor(Date.now()/1000+60*60*24*365) })
            } else {
                Cookies.set("viewType","lb", { maxAge: Math.floor(Date.now()/1000+60*60*24*365) })
            }
    }, [MB,Cookies])

    return (
        <Router>
            <Routes>
                <Route path="/wiki" element={<Main route="wiki" view="wiki"/>}/>
                <Route path="/wiki/:topic" element={<Main route="wiki" view="wiki"/>}/>
                <Route path="/wiki/:topic/:id" element={<Main route="wikiviewer" view="wiki"/>}/>
                <Route path="/feedback" element={<Main route="feedback" view="feedback"/>}/>
                <Route path="/generator" element={<Main route="generate" view="generator"/>}/>
                <Route path="/editor" element={<Main route="editor"/>}/>
                <Route path="/howtocreate" element={<Main route="howtocreate" view="home"/>}/>
                <Route path="/settings" element={<Main route="settings" view="settings"/>}/>
                <Route path="/" element={<Main route="home" view="home"/>}/>
            </Routes>
        </Router>
    );
}

export default App;
